$primary: #234180;
$secondary: #fddb2f;
$body-bg: #192E5A;
$text-primary: #F6F6F4;

@import '~bootstrap/scss/bootstrap.scss';

.body {
  background-color: $body-bg;
  color: $text-primary;
}

header {
  color: $secondary;
  text-align: center;
  margin: 10px;
}

header h1 {
  font-size: 2.5em;
  padding: 10px 0;
}